:root {
  --bg: #ffffff;
  --text: #111827;
  --card: rgba(255, 255, 255, 0.85);
  --border: rgba(15, 23, 42, 0.14);
  --active: #111827;
  --shadow: 0 10px 30px rgba(15, 23, 42, 0.08);
  --topbar-height: 5.5rem;
}
html[data-theme="theme1"] {
  --bg: #eef2f9;
  --text: #0f172a;
  --card: rgba(255, 255, 255, 0.9);
  --border: rgba(15, 23, 42, 0.12);
  --active: #2563eb;
  --shadow: 0 16px 40px rgba(15, 23, 42, 0.10);
}
html[data-theme="theme2"] {
  --bg: #0b1220;
  --text: #f8fafc;
  --card: rgba(255, 255, 255, 0.06);
  --border: rgba(255, 255, 255, 0.14);
  --active: #f8fafc;
  --shadow: 0 12px 34px rgba(0, 0, 0, 0.35);
}
html[data-theme="theme3"] {
  --bg: #fff7ed;
}
html[data-theme="theme4"] {
  --bg: #ffffff;
  --text: #111827;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background: var(--bg);
  color: var(--text);
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
}
.topbar {
  position: sticky;
  top: 0;
  z-index: 50;
  height: var(--topbar-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 14px 18px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  background: linear-gradient(135deg, #1e3a8a 0%, #1d4ed8 45%, #2563eb 100%);
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(10px);
  overflow-x: auto;
  overflow-y: hidden;
}
.topbar .brand {
  color: #f8fafc;
  font-weight: 800;
  letter-spacing: 0.2px;
  flex-shrink: 0;
  white-space: nowrap;
}
.brand {
  font-weight: 800;
  letter-spacing: 0.2px;
}
.switcher {
  display: inline-flex;
  gap: 8px;
  padding: 6px;
  border: 1px solid var(--border);
  background: var(--card);
  border-radius: 999px;
  backdrop-filter: blur(10px);
}
.btn {
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.65);
  color: var(--text);
  padding: 10px 14px;
  border-radius: 999px;
  cursor: pointer;
  font-weight: 650;
}
.btn:hover {
  background: rgba(255, 255, 255, 0.85);
}
.btn.active,
a.btn.primary {
  background: var(--active);
  border-color: var(--active);
  color: var(--bg);
}
.topbar .switcher {
  background: transparent;
  border: none;
  padding: 0;
  border-radius: 0;
  backdrop-filter: none;
  flex-shrink: 0;
  min-width: fit-content;
}
.topbar .btn {
  color: #e2e8f0;
  background: rgba(0, 0, 0, 0.25);
  border-color: rgba(255, 255, 255, 0.2);
}
.topbar .btn:hover {
  background: rgba(0, 0, 0, 0.35);
  color: #fff;
  border-color: rgba(255, 255, 255, 0.3);
}
.topbar .btn.active {
  background: #fff;
  border-color: #fff;
  color: #1e40af;
}
.container {
  max-width: 1050px;
  margin: 0 auto;
  padding: 28px 18px 60px;
  display: flex;
  flex-direction: column;
  gap: 22px;
}
.hero {
  padding-top: 10px;
}
.heroCard {
  border: 1px solid var(--border);
  background: var(--card);
  border-radius: 20px;
  padding: 28px;
  box-shadow: var(--shadow);
}
.h1 {
  font-size: 44px;
  margin: 0 0 10px;
  line-height: 1.05;
}
.h2 {
  font-size: 24px;
  margin: 0 0 8px;
}
.p {
  margin: 0;
  opacity: 0.85;
  font-size: 18px;
  line-height: 1.55;
}
.actions {
  display: flex;
  gap: 12px;
  margin-top: 18px;
  flex-wrap: wrap;
}
a.btn {
  display: inline-block;
  text-decoration: none;
}
.grid {
  display: grid;
  gap: 14px;
  grid-template-columns: repeat(3, 1fr);
}
.card {
  border: 1px solid var(--border);
  background: var(--card);
  border-radius: 20px;
  padding: 18px;
  box-shadow: var(--shadow);
}
.card h3 {
  margin: 0 0 6px;
}
.card p {
  margin: 0;
  opacity: 0.8;
  line-height: 1.45;
}
.split {
  display: grid;
  gap: 14px;
  grid-template-columns: repeat(2, 1fr);
}
.list {
  margin: 10px 0 0;
  padding-left: 18px;
  opacity: 0.85;
  line-height: 1.7;
}
.ctaCard {
  border: 1px solid var(--border);
  background: var(--card);
  border-radius: 20px;
  padding: 22px;
  box-shadow: var(--shadow);
  text-align: center;
}
.footer {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  padding-top: 10px;
  opacity: 0.8;
}
.muted {
  opacity: 0.7;
}
@media (max-width: 900px) {
  .h1 {
    font-size: 34px;
  }
  .grid {
    grid-template-columns: 1fr;
  }
  .split {
    grid-template-columns: 1fr;
  }
}
.themePage {
  display: none;
}
html[data-theme="theme1"] .themePage[data-page="theme1"] {
  display: block;
}
html[data-theme="theme2"] .themePage[data-page="theme2"] {
  display: block;
}
html[data-theme="theme3"] .themePage[data-page="theme3"] {
  display: block;
}
html[data-theme="theme4"] .themePage[data-page="theme4"] {
  display: block;
}
